<button class="headerButton" on:click={onClick}>
    <span class="mdi mdi-{icon}"></span>
</button>

<script>
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon;

// Functions
function onClick() {
    dispatch("click");
}
</script>

