<LayoutCenter>
    <div class="padding">
        <SpaceBetween>
            <Setting bind:value={$settingsStore.preferPopup} text="Extra informatie in popup">
                <p>Als deze instelling aan staat krijg je een popup als je op meer info drukt in plaats van een dropdown.</p>
            </Setting>

            <DropDown text="Geavanceerde instellingen">
                <SpaceBetween>
                    <Button icon="delete-outline" on:click={resetEverything}>Reset alles</Button>
                    <h6>Versie 3.0.0-Beta.3</h6>
                </SpaceBetween>
            </DropDown>
        </SpaceBetween>
    </div>
</LayoutCenter>

<script>
// Imports
import { settingsStore } from "@/js/settingsUtils.js";

// Components
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import SpaceBetween from "@/components/Layout/SpaceBetween.svelte";
import Setting from "@/components/Setting.svelte";
import DropDown from "@/components/DropDown.svelte";
import Button from "@/components/Button.svelte";
import ButtonContainer from "@/components/ButtonContainer.svelte";

// Functions
function resetEverything() {
    if (confirm("Weet je zeker dat je alles wilt resetten?")) {
        let keys = Object.keys(localStorage);
    
        for (let key of keys) {
            if (key.includes("aut-ov-")) {
                localStorage.removeItem(key);
            }
        }

        location.reload();
    }
}
</script>

