import Walking from "@/components/RouteStep/Walking.svelte";
import Train from "@/components/RouteStep/Train.svelte";
import Bus from "@/components/RouteStep/Bus.svelte";
import PlatformChange from "@/components/RouteStep/PlatformChange.svelte";

import { prepareStoreLinked } from "@/js/storeUtils.js";
import { writable } from "svelte/store";

const stepTypeComponentMap = {
    walking: Walking,
    train: Train,
    bus: Bus,
    platformChange: PlatformChange,
}

export function getStepInfoComponent(stepType) {
    return stepTypeComponentMap[stepType];
}

export function getStepType(step) {
    return step.travelMode;
}

export function getStepColor(step) {
    if (step.travelMode === "walking" || step.travelMode === "platformChange") return "var(--stepWalkingColor)";

    return "#" + step.transitColor.background;
}

export function getStepTextColor(step) {
    if (step.travelMode === "walking" || step.travelMode === "platformChange") return "";

    return "#" + step.transitColor.foreground;
}

export function getStepIcon({ travelMode }) {
    if (travelMode === "walking") return "walk";
    if (travelMode === "platformChange") return "swap-horizontal";

    return travelMode.toLowerCase();
}

export function getStepBorderStyle(step) {
    var stepType = getStepType(step);

    if (stepType === "bus") return "dashed";
    else if (stepType === "train") return "solid";
    else return "dotted";
}

export function getStepBorderStyling(step) {
    return `border-style: ${getStepBorderStyle(step)};
    border-color: ${getStepColor(step)};`
}

export function getTrainType(transitDetails) {
    return transitDetails.line.short_name.toLowerCase();
}

export function createStepStore(storeName, data = {}) {
    var store = writable({
        ...data,
    });

    prepareStoreLinked(store, "store_" + storeName);

    return store;
}