<button on:click={clickEvent}>
    <span class="mdi mdi-{icon}"></span>
    <slot />
</button>

<script>
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon = "";

// Function
function clickEvent() {
    dispatch("click");
}
</script>

<style lang="scss">

button {
    margin: 0;

    background-color: transparent;
    border: 0;
    border-radius: 5px;
    color: var(--foregroundColor);
    line-height: 16px;

    span {
        font-size: 18px;
        line-height: 16px;
    }
}

</style>