<section class="trips">
    <LayoutCenter>
        <TripComposer on:searching={onSearching} on:results={showResults} on:error={handleError} />

        {#if loading}
            <LoadingScim id="loadingTrips" />
        {:else if error}
            <ErrorState>
                Er is een fout opgetreden
            </ErrorState>
        {:else}
            {#each trips as trip}
                <TripResult {trip} on:selectTrip={selectTrip} />
            {/each}
        {/if}
    </LayoutCenter>
</section>

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Stores
import { availableTripsStore, getTripFromAddress } from "@/js/controllers/trips.js";

// Components
import TripResult from "@/components/Trips/TripResult.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import TripComposer from "@/components/Trips/TripComposer.svelte";
import LoadingScim from "@/components/Misc/LoadingScim.svelte";
import ErrorState from "@/components/Misc/ErrorState.svelte";

// Data
var trips = [];
var loading = false;
var error = false;

// Function
function onSearching() {
    loading = true;
    error = false;

    setTimeout(() => {
        document.getElementById("loadingTrips").scrollIntoView({
            behavior: "smooth",
            block: "center",
        });
    }, 0);
}

function showResults({ detail: results }) {
    loading = false;
    trips = results;
}

function selectTrip({ detail }) {
    dispatch("selectTrip", detail);
}

function handleError() {
    loading = false;
    error = true;
}
</script>

