import { addressStore, matchLocationFromId, getAutoAddress, getAddressById } from "@/js/controllers/addresses.js";
import { locationStore, getDirectDistance, isValidPosition } from "@/js/locationUtils.js";
import { writable, writableLinked } from "@/js/storeUtils.js";
import { serverRequest, gql } from "@/js/dataUtils.js";

// Stores
export const fromIdStore = writableLinked("fromId", "currentLocation");
export const toIdStore = writableLinked("toId", "auto");
export const routeStore = writableLinked("currentRoute", {});

export const availableTripsStore = writable([]);
export const currentRouteMode = writable({
    address: "auto",
});

// Functions
export async function getCurrentRouteTrip() {
    var location = locationStore.get();
    var addresses = addressStore.get();

    var directDistanceHome = getDirectDistance(location, addresses.home.location)
}

export async function doSearch(fromId = fromIdStore.get(), toId = toIdStore.get()) {
    console.log("a", { fromId, toId });
    var response = await getTripsFromIds(fromId, toId);
    
    if (response === undefined || response.routes === undefined) return [];
    if (response.status !== "OK") return [];

    try {
        availableTripsStore.set(response.routes);
    } catch(e) {
        return [];
    }
}

export async function getTripsFromIds(fromId, toId) {
    if (fromId === toId) return;

    let fromLocation = matchLocationFromId(fromId);
    let toLocation = matchLocationFromId(toId);

    return await getTrips(fromLocation, toLocation);
}

export async function getTrips(startLocation, endLocation) {
    if (!isValidPosition(startLocation)) return;
    if (!isValidPosition(endLocation)) return;

    let response = await gql`
        ${{
            fromLat: startLocation.lat,
            fromLong: startLocation.long,
            toLat: endLocation.lat,
            toLong: endLocation.long,
        }}

        query($fromLat: Float!, $fromLong: Float!, $toLat: Float!, $toLong: Float!) {
            tripsFromLocations(fromLat: $fromLat, fromLong: $fromLong, toLat: $toLat, toLong: $toLong) {
                status
                trips {
                    departureTime {
                        timestamp
                    }
                    arrivalTime {
                        timestamp
                    }
                    travelDuration {
                        string
                    }
                    toAddress {
                        streetName
                    }
                    steps {
                        randomId
                        travelMode
                        instructions
                        travelDuration {
                            seconds
                        }
                        polyline
                        ... on TrainStep {
                            trainId
                            departureTime {
                                timestamp
                            }
                            arrivalTime {
                                timestamp
                            }
                            transitColor {
                                background
                                foreground
                            }
                            journeyRefreshId
                            journey {
                                cancelled
                            }
                            fromStation {
                                id
                                name
                            }
                            toStation {
                                id
                                name
                            }
                        }
                        ... on BusStep {
                            lineNumber
                            departureTime {
                                timestamp
                            }
                            arrivalTime {
                                timestamp
                            }
                            transitColor {
                                background
                                foreground
                            }
                            fromBusStop {
                                id
                                name
                            }
                            toBusStop {
                                id
                                name
                            }
                            refreshId
                        }
                    }
                }
            }
        }
    `;

    return response.data.tripsFromLocations.trips;
}

var locationWatcher;
var firstCorrectLocation = true;
var currentAutoRoute = null;
export function startAutoPlanner() {
    locationWatcher = locationStore.subscribe(currentPosition => {
        if (!currentPosition || !isValidPosition(currentPosition)) return;

        if (toIdStore.get() === "auto") {
            let autoRoute = getAutoAddress().id;
            
            if (currentAutoRoute === null || currentAutoRoute !== autoRoute) {
                console.log("a", autoRoute);
                doSearch(fromIdStore.get(), autoRoute);
            }

            currentAutoRoute = autoRoute;
        } else {
            doSearch();
        }
    });
}

export function stopAutoPlanner() {
    locationWatcher();
}

export function getTripFromAddress() {
    var id = fromIdStore.get();
    return getAddressById(id);
}

export function getTripToAddress() {
    var id = toIdStore.get();
    return getAddressById(id);
}