<div class="locationSelect" on:click={() => popupOpen = true}>
    <div class="iconWrapper">
        <span class="mdi mdi-{address.icon}"></span>
    </div>
    <div class="textWrapper">
        <p class="name">{address.name}</p>
        <p class="address">{address.address}</p>
    </div>
</div>

<Popup bind:open={popupOpen}>
    <h4>Select address</h4>
    <AddressSearchBar on:pickedResult={selectSearchBarResult} />
    
    <LabelDivider text="Opgeslagen" />
    
    {#each suggestedAddresses as address}
        <AddressRouteOption icon={address.icon} address={address.address} on:click={() => selectAddress(address)} />
    {:else}
        <EmptyState>
            Ga naar je addressboek om locaties op te slaan
        </EmptyState>
    {/each}
</Popup>

<script>
// Imports
import { locationStore } from "@/js/locationUtils.js";
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Components
import Popup from "@/components/Misc/Popup.svelte";
import AddressSearchBar from "@/components/Address/AddressSearchBar.svelte";
import LabelDivider from "@/components/LabelDivider.svelte";
import AddressRouteOption from "@/components/Address/AddressRouteOption.svelte";
import EmptyState from "@/components/Misc/EmptyState.svelte";
import { unsetAddress } from "@/js/controllers/addresses.js";

// Props
export var address = unsetAddress;
export var suggestedAddresses = [];

// Data
var popupOpen = false;

// Computed
$: {
    $locationStore;
    address = address;
}

// Functions
function selectAddress(address) {
    dispatch("select", address);
    popupOpen = false;
}

function selectSearchBarResult({ detail }) {
    dispatch("select", detail);
    popupOpen = false;
}
</script>

<style lang="scss">

h4 {
    margin-top: 0;
    text-align: center;
}

.locationSelect {
    height: 48px;
    width: 100%;

    display: flex;
    flex-direction: row;

    cursor: pointer;

    .iconWrapper {
        width: 48px;

        flex-grow: 0;
        flex-shrink: 0;

        span {
            position: relative;
            display: block;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);

            text-align: center;
            font-size: 24px;
        }
    }

    .textWrapper {
        position: relative;
        flex-grow: 1;
        flex-shrink: 1;

        overflow: hidden;

        p {
            width: 100%;

            margin: 0;

            &.name {
                position: absolute;
                top: 4px;

                font-weight: 700;
                font-size: 18px;
            }

            &.address {
                position: absolute;
                bottom: 4px;

                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
}

</style>