<InfoRow>
    <span slot="left">{startTimeString}</span>
    <span slot="center">
        <HeadSign icon="bus" {backgroundColor} {textColor}>{line}</HeadSign>
    </span>
    <span slot="right">{endTimeString}</span>
</InfoRow>

<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-expand-right"></span> {step.fromBusStop.name}</span>
</InfoRow>
<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-collapse-right"></span> {step.toBusStop.name}</span>
</InfoRow>

{#if currentPassLoaded}
    <InfoRow>
        <span slot="left">
            <span class="mdi mdi-rss"></span>
            <span class="delta">{deltaString}</span>

            {#if currentPass.status === "arrived"}
                Aangekomen bij 
            {:else if currentPass.status === "driving"}
                Rijd naar 
            {:else if currentPass.status === "passed"}
                Bus is gepasseerd
            {:else if currentPass.status === "planned"}
                Geplanned
            {:else if currentPass.status === "cancelled"}
                Stopt niet bij
            {/if}
        
            {currentPass.name}
        </span>
    </InfoRow>
{/if}


{#if journeyLoaded}
    <MoreInfo>
        <InfoRow>
            <span slot="left">
                <span class="mdi mdi-routes"></span>
                Richting
            </span>
            <span slot="right">
                {journey.line.destination.name}
            </span>
        </InfoRow>

        {#if currentPass}
            <BusTimeLine {stops} currentStop={currentPass} />
        {/if}
    </MoreInfo>
{/if}

<script>
import { onMount, onDestroy } from "svelte";
import { getBusStop, findDeparture, getBusStopsFromJourney, getBusTrip, getActiveStops, getCurrentStop } from "@/js/busUtils.js";
import { getStepColor, getStepTextColor, createStepStore } from "@/js/stepUtils.js";
import { timestampToString } from "@/js/utils/timeUtils.js";
import { createPoller } from "@/js/controllers/liveData.js";

// Components
import InfoRow from "@/components/Misc/InfoRow.svelte";
import HeadSign from "@/components/HeadSign.svelte";
import MoreInfo from "@/components/MoreInfo.svelte";
import BusStop from "@/components/RouteStep/BusStop.svelte";
import BusTimeLine from "@/components/RouteStep/BusTimeLine.svelte";

// Props
export var step;

// Data
var journey = null;
var currentPass = null;

var journeyPoller = null;
var locationPoller = null;

// Computed
$: journeyLoaded = journey !== null;
$: currentPassLoaded = currentPass !== null;
$: line = step.lineNumber;
$: backgroundColor = getStepColor(step);
$: textColor = getStepTextColor(step);
$: startTimeString = timestampToString(step.departureTime.timestamp);
$: endTimeString = timestampToString(step.arrivalTime.timestamp);
$: stops = journeyLoaded ? journey.passes : [];
$: delta = currentPassLoaded ? currentPass.arrivalDelta.milliseconds : 0;
$: deltaString = toDeltaString(delta);

// Functions
function toDeltaString(milliseconds) {
    let leadingCharacter = "";
    if (milliseconds < 0) {
        milliseconds = Math.abs(milliseconds);
        leadingCharacter = "-";
    } else if (milliseconds > 0) {
        leadingCharacter = "+";
    }

    let seconds = Math.floor(milliseconds / 1000);
    let minutes = Math.floor(seconds / 60);

    seconds -= minutes * 60;
    seconds = String(seconds).padStart(2, "0");

    return `${leadingCharacter}${minutes}:${seconds}`;
}

// Events
onMount(() => {
    journeyPoller = createPoller({
        query: `
            query($token: String!) {
                liveBusJourney(busJourneyId: $token) {
                    shouldPoll
                    refreshId
                    pollAfter
                    journey {
                        id
                        name
                        currentPass {
                            stopId
                            name
                            status
                            arrivalDelta {
                                onTime
                                milliseconds
                            }
                        }
                        passes {
                            stopId
                            name
                            status
                        }
                        line {
                            destination {
                                id
                                name
                            }
                        }
                    }
                }
            }
        `,
        initialToken: step.refreshId,
        key: "liveBusJourney",
        memoization: step.randomId,
        memoizationTags: ["liveBusJourney", "liveRouteJourney"],
        onData(data, instance) {
            if (!!data?.journey) {
                journey = data.journey;
                currentPass = journey.currentPass;
            }
        },
    });
});

onDestroy(() => {
    if (journeyPoller) journeyPoller.stop();
    if (locationPoller) locationPoller.stop();
});
</script>

<style lang="scss">

span.delta {
    padding: 2px 4px;

    background-color: #4c6dff;
    color: #ffffff;
    border-radius: 5px;
}

</style>