<div class="routeGuide">
    {#if started}
        <div class="tripStartInfo">
            <p><span class="mdi mdi-flag"></span> Vertrek om <b>{departureTime}</b></p>
        </div>
        {#each steps as step, index (step)}
            <Step {step} currentStep={step === currentStep} isFirstStep={index === 0} isLastStep={index === steps.length - 1} />
        {/each}
        <div class="tripStartInfo">
            <p><span class="mdi mdi-flag-checkered"></span> Aankomst om <b>{arrivalTime}</b></p>
        </div>
    {:else}
        <LayoutCenter>
            <EmptyState>Geen route gestart</EmptyState>
        </LayoutCenter>
    {/if}
    <LayoutCenter>
        <ButtonContainer buttons={1}>
            {#if started}
                <Button on:click={endRoute} icon="stop-circle-outline">Stop route</Button>
            {/if}
        </ButtonContainer>
    </LayoutCenter>
</div>

<script>
// Imports
import { getCurrentStep } from "@/js/controllers/route.js";
import { locationStore } from "@/js/locationUtils.js";
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Components
import Step from "@/components/Step.svelte";
import ButtonContainer from "@/components/ButtonContainer.svelte";
import Button from "@/components/Button.svelte";
import EmptyState from "@/components/Misc/EmptyState.svelte";
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import { timestampToString } from "../js/utils/timeUtils";

// Props
export var route;

// Computed
$: started = Object.values(route).length > 0;
$: steps = route.steps;
$: currentStep = getCurrentStep(route, $locationStore);
$: departureTime = started ? timestampToString(route.departureTime.timestamp) : "";
$: arrivalTime = started ? timestampToString(route.arrivalTime.timestamp) : "";

// Functions
function endRoute() {
    if (confirm("Weet je zeker dat je deze route wilt stoppen?")) {
        dispatch("endRoute");
    }
}
</script>

<style lang="scss">

.tripStartInfo {
    width: calc(95% - 48px);
    max-width: 514px;

    margin: 8px auto;

    p {
        margin: 0;

        color: var(--textColor);

        span {
            font-size: 18px;
            margin-right: 8px;
        }
    }
}

.routeGuide {
    width: 100%;

    padding: 16px 0;
    
    & > :global(.emptyState) {
        width: calc(95% - 32px - 30px);
    }

    // Probably can be done cleaner
    & > :global(div) > :global(.buttonContainer) {
        margin-top: 32px;
    }
}

</style>