import { writable as storeWritable } from "svelte/store";

export function prepareStore(store) {
    store.get = () => {
        var resultValue = {};
        var unsubscribe = store.subscribe(data => {
            resultValue = data;
        });

        unsubscribe();
        return resultValue;
    }

    store.getList = () => Object.values(store.get());
    store.set = value => store.update(_ => value);
    store.arrayPush = item => {
        var items = store.get();
        items.push(item);

        store.set(items);
    }

    store.arrayRemove = item => {
        var items = store.get();
        var index = items.indexOf(item);

        items.splice(index, 1);
        store.set(items);
    }
}

export function linkStore(store, storeLocation) {
    store.subscribe(data => {
        if (data !== undefined) {
            if (typeof data === "object") {
                data = JSON.stringify(data);
            }

            localStorage.setItem(storeLocation, data);
        }
    });
}

export function prepareStoreLinked(store, storeLocation, defaultData = null) {
    prepareStore(store);

    var storeData = store.get();
    if (storeData) {
        defaultData = storeData;
    }

    var localData = localStorage.getItem(storeLocation);

    if (localData) {
        if (typeof defaultData === "object") {
            store.set(JSON.parse(localData));
        } else {
            store.set(localData);
        }
    } else {
        store.set(defaultData);
    }

    linkStore(store, storeLocation);

    return store;
}

export function writable(data) {
    var store = storeWritable(data);
    prepareStore(store);

    return store;
}

export function writableLinked(location, data) {
    location = "aut-ov-" + location;

    var store = storeWritable(data);
    prepareStoreLinked(store, location);

    return store;
}