<section>
    <AddressSelect address={startAddress} on:select={event => startAddress = event.detail} suggestedAddresses={getStartAddresses()} />
    <LabelDivider text="Naar" />
    <AddressSelect address={endAddress} on:select={event => endAddress = event.detail} suggestedAddresses={getEndAddresses()} />
    
    <Button icon="magnify" on:click={searchTrips} enabled={canPerformSearch}>Zoek</Button>
</section>

<script>
// Imports
import { getStartAddresses, getEndAddresses, unsetAddress, currentLocationAddress } from "@/js/controllers/addresses.js";
import { getTrips } from "@/js/controllers/trips.js";
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Components
import LabelDivider from "@/components/LabelDivider.svelte";
import Button from "@/components/Button.svelte";
import AddressSelect from "@/components/Address/AddressSelect.svelte";

// Data
var startAddress = getStartAddresses().length > 0 ? getStartAddresses()[0] : unsetAddress;
var endAddress = getEndAddresses().length > 0 ? getEndAddresses()[0] : unsetAddress;

// Computed
$: canPerformSearch = endAddress !== unsetAddress && startAddress.address !== endAddress.address;

// Functions
async function searchTrips() {
    try {
        dispatch("searching");
        let trips = await getTrips(startAddress.location, endAddress.location);
    
        if (trips) {
            dispatch("results", trips);
        }
    } catch (e) {
        dispatch("error");
    }
}
</script>

<style lang="scss">

section {
    margin: 16px auto;
}

</style>