import { writableLinked } from "@/js/storeUtils.js";
import { locationStore, getDirectDistance, isValidPosition } from "@/js/locationUtils.js";

export const addressStore = writableLinked("addresses", {
    home: {
        name: "Huis",
        icon: "home-outline",
        canBeDeleted: false,
        address: "",
        location: {
            lat: 0,
            long: 0,
        },
    },
    work: {
        name: "Werk",
        icon: "briefcase-outline",
        canBeDeleted: false,
        address: "",
        location: {
            lat: 0,
            long: 0,
        },
    },
});

export const currentLocationAddress = {
    name: "Huidige locatie",
    id: "currentLocation",
    icon: "crosshairs-gps",
    canBeDeleted: false,
    address: "Gebruik GPS locatie",
    get location() {
        return locationStore.get();
    },
}

export const autoAddress = {
    name: "Auto",
    id: "auto",
    icon: "arrow-decision-auto-outline",
    canBeDeleted: false,
    get address() {
        return getAutoAddress().address + " (auto)";
    },
    get location() {
        return getAutoAddress().location;
    }
}

export const unsetAddress = {
    name: "Geen locatie",
    id: "invalid",
    icon: "map-marker-remove-outline",
    canBeDeleted: false,
    address: "Geen address geselecteerd",
    location: {
        lat: 0,
        long: 0,
    },
}

// Function for later use when you can add more addresses to the address book
function getStoredAddresses() {
    return getAutoAddresses();
}

function getAutoAddresses() {
    let addresses = [];
    let storedLocations = addressStore.get();

    if (isValidPosition(storedLocations.home.location)) {
        addresses.push(storedLocations.home);
    }

    if (isValidPosition(storedLocations.work.location)) {
        addresses.push(storedLocations.work);
    }

    return addresses;
}

export function getStartAddresses() {
    return [
        currentLocationAddress,
        ...getStoredAddresses(),
    ];
}

export function getEndAddresses() {
    let autoAddresses = getAutoAddresses();

    let addresses = [];

    if (autoAddresses.length === 2) {
        addresses.push(autoAddress);
    }

    addresses = [
        ...addresses,
        ...getStoredAddresses(),
    ];

    return addresses;
}

export function getAddressById(id) {
    if (id === "auto") {
        return getAutoAddress();
    } else if (id === "currentLocation") {
        let instance = { ...currentLocationAddress };
        instance.location = locationStore.get();

        return instance;
    } else {
        let addresses = addressStore.get();
        let match = addresses[id];

        return match;
    }
}

export function getAutoAddress() {
    let addresses = addressStore.get();
    let home = addresses.home;
    let work = addresses.work

    home.id = "home";
    work.id = "work";

    let homeLocation = home.location;
    let workLocation = work.location;
    let currentLocation = locationStore.get();

    let directDistanceHome = getDirectDistance(homeLocation, currentLocation);
    let directDistanceWork = getDirectDistance(workLocation, currentLocation);

    return directDistanceHome < directDistanceWork ? addresses.work : addresses.home;
}

export function getAddresses() {
    let addresses = addressStore.get();

    return Object.entries(addresses)
        .map(([key, value]) => {
            return {
                canBeDeleted: true,
                id: key,
                ...value,
            }
        });
}

export function getCompleteAddressMap() {
    return {
        ...addressStore.get(),
        currentLocation: currentLocationAddress,
        auto: autoAddress,
    }
}

export function getOriginAddresses() {
    return [
        ...getAddresses(),
        currentLocationAddress,
    ]
}

export function getDestinationAddresses() {
    return [
        ...getAddresses(),
        autoAddress,
    ]
}

export function matchLocationFromId(id) {
    var address = getAddressById(id);
    if (address) return address.location;
}

const defaultAddress = {
    name: "Nieuw addres",
    id: "invalid",
    icon: "crosshairs-gps",
    canBeDeleted: true,
    address: "",
    location: {
        lat: 0,
        long: 0,
    },
}

export function saveAddress(id, object) {
    object = {
        ...defaultAddress,
        ...object,
        id,
    }

    let storeContent = addressStore.get();
    storeContent[id] = object;

    addressStore.set(storeContent);
}