<div class="address {currentTab !== "" ? "tabOpen" : ""}" use:outsideClick={closeTab}>
    <h3 class="header">
        <span class="mdi mdi-{address.icon}"></span>
        {address.name}
    </h3>

    <InfoRow>
        <span slot="left"><span class="mdi mdi-road-variant"></span> {physicalAddress}</span>
    </InfoRow>

    <div class="buttons">
        <SoftButton icon="pencil" on:click={() => editOpen = true}>Pas aan</SoftButton>
        <SoftButton icon="format-list-numbered">Regels</SoftButton>
    </div>
</div>

<Popup bind:open={editOpen}>
    <AddressEditor addressId={address.id} on:saved={() => editOpen = false} />
</Popup>

<script>
import outsideClick from "svelte-outside-click";

// Components
import AddressEditor from "./AddressEditor.svelte";
import InfoRow from "@/components/Misc/InfoRow.svelte";
import SoftButton from "@/components/Misc/SoftButton.svelte";
import ButtonContainer from "@/components/ButtonContainer.svelte";
import Popup from "@/components/Misc/Popup.svelte";

// Props
export var address;

// Data
var currentTab = "";
var editOpen = false;

// Computed
$: physicalAddress = address.address ? address.address : "(geen address ingesteld)";

// Functions
function openTab(tab) {
    currentTab = tab;
}

function closeTab() {
    currentTab = "";
}
</script>

