<div class="busTimeline">
    {#if lastStopAvailable}
        <p class="{getClass(lastStop.status)}">
            <span class="mdi mdi-{getStatusIcon(lastStop.status)}"></span>
            {lastStop.name}
        </p>
    {/if}


    <div class="current {getClass(currentStop.status)}">
        <span class="mdi mdi-{getStatusIcon(currentStop.status)}"></span>
        {currentStop.name}
    </div>
    
    {#if nextStopAvailable}
        <p class="{getClass(nextStop.status)}">
            <span class="mdi mdi-{getStatusIcon(nextStop.status)}"></span>
            {nextStop.name}
        </p>
    {/if}
</div>

<script>
// Props
export var stops = [];
export var currentStop = { stopId: "", name: "" }

// Data
const statusIconMap = {
    arrived: "check",
    passed: "check-all",
    planned: "alarm",
    driving: "bus",
    cancelled: "alert"
}

// Computed
$: currentStopObject = stops.find(stop => stop.stopId === currentStop.stopId);
$: currentStopFound = currentStopObject !== undefined;
$: currentStopIndex = stops.indexOf(currentStopObject);
$: lastIndex = currentStopIndex - 1;
$: nextIndex = currentStopIndex + 1;
$: lastStopAvailable = lastIndex >= 0;
$: nextStopAvailable = nextIndex < stops.length;
$: lastStop = stops[lastIndex];
$: nextStop = stops[nextIndex];

// Functions
function getStatusIcon(status) {
    let icon = statusIconMap[status];
    
    if (icon === undefined) {
        icon = "help-rhombus-outline";
    }

    return icon;
}

function getClass(status) {
    if (status === "cancelled") {
        return "cancelled"
    }

    return "";
}
</script>

<style lang="scss">

.current {
    width: 100%;
    
    padding: 8px 12px;
    border-radius: 10px;
    box-shadow: var(--defaultBoxShadow);
    box-sizing: border-box;
}

p {
    margin: 4px;
    color: var(--lowContrastColor);
    font-size: 12px;
    padding: 0 6px;
}

.cancelled {
    color: var(--errorColor);
    font-weight: 700;
}

</style>