    <div class="leftButtons">
        <HeaderButton icon={"menu"} on:click={toggleMenuOpen} />
    </div>
    <div class="rightButtons">
        <HeaderButton icon={"magnify"} />
    </div>
    <div class="menuContent">
        <MenuButton icon="compass-outline" on:click={closeMenu} path="/">Navigatie</MenuButton>
        <MenuButton icon="bookmark-multiple-outline" on:click={closeMenu} path="/addressbook">Address boek</MenuButton>
        <MenuButton icon="cog-outline" on:click={closeMenu} path="/settings">Instellingen</MenuButton>
    </div>
</header>
<div class="menuScim {menuOpen ? "open" : ""}" on:click|self={closeMenu}></div>

<script>
// Components
import HeaderButton from "@/components/HeaderButton.svelte";
import MenuButton from "@/components/MenuButton.svelte";

// Data
var menuOpen = false;

// Functions
function toggleMenuOpen() {
    menuOpen = !menuOpen;
}

function closeMenu() {
    menuOpen = false;
}
</script>

