{#if open}
    <div class="scim" use:inject={"main"} on:click|self={close}>
        <div class="box">
            <slot />
        </div>
    </div>
{/if}

<script>
// Imports
import inject from "svelte-inject";
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var open = false;

// Functions
function close() {
    open = false;
    dispatch("close");
}
</script>

<style lang="scss">

.scim {
    height: 100%;
    width: 100%;

    position: fixed;
    top: 0;
    left: 0;
    z-index: 12;

    background-color: #cecece67;

    .box {
        max-width: 400px;
        width: 90%;

        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 16px;
        box-sizing: border-box;

        background-color: var(--backgroundColor);
        box-shadow: var(--defaultBoxShadow);
        border-radius: 16px;
        animation: 200ms popIn cubic-bezier(0.4, 0.0, 0.2, 1);
    }
}

@keyframes popIn {
    0% {
        transform: translate(-50%, -50%) scale(0.95);
        opacity: 0;
    }

    100% {
        transform: translate(-50%, -50%) scale(1);
        opacity: 1;
    }
}

</style>