<SearchBar bind:value={query} {results} on:pickedResult={result => pickResult(result)} />

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Components
import SearchBar from "@/components/SearchBar.svelte";

// Props
export var query = "";

// Data
var results = [];
var timer;
var firstResult = true;

// Computed
$: trySearch(query);

// Functions
function trySearch() {
    if (timer) clearTimeout(timer);
    timer = setTimeout(() => doSearch(), 1000);
}

async function doSearch() {
    if (firstResult) {
        firstResult = !firstResult;
        return;
    }

    if (query === "") return;

    var response = await fetch(`https://1313.nl/geocoder/${query}*`)
        .then(r => r.json());

    results = response.features.map(result => {
        var [long, lat] = result.geometry.coordinates;

        return {
            icon: "map-marker-circle",
            name: result.properties.search,
            location: { lat, long, },
            ...result,
        }
    });
}

function pickResult({ detail: result }) {
    results = [];
    query = result.name;

    // Timer needs to be cleared after svelte has updated the listeners
    queueMicrotask(() => {
        if (timer) clearTimeout(timer);
    });

    result.address = result.name;
    result.name = result.properties.openbareruimtenaam;
    
    dispatch("pickedResult", result);
}
</script>

<style lang="scss">

.result {
    $height: 32px;

    height: $height;
    width: 100%;
}

</style>