<div on:click={onClick} class="addressRouteOption">
    <div class="iconWrapper">
        <span class="mdi mdi-{icon}"></span>
    </div>
    <div class="textWrapper">
        <h4>{address}</h4>
    </div>
</div>

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon = "map-marker-circle";
export var address = "Null-island";

// Functions
function onClick() {
    dispatch("click", address);
}
</script>

