<div class="tripOverviewBar">
    {#each steps as step}
        <div class="tripSection" style="{getStyle(step)}"></div>
    {/each}
</div>

<script>
import { getStepBorderStyle, getStepColor } from "@/js/stepUtils.js";

// Props
export var steps;
console.log(steps);

// Functions
function getStyle(step) {
    return `width: ${getWidth(step)}%;
    border-color: ${getStepColor(step)};
    border-style: ${getStepBorderStyle(step)}`;
}

function getWidth(step) {
    var stepDuration = step.travelDuration.seconds;
    var totalTime = steps.reduce((acc, step) => acc + step.travelDuration.seconds, 0);
    
    return (stepDuration / totalTime) * 100;
}
</script>

<style lang="scss">

.tripOverviewBar {
    height: 2px;
    width: 100%;

    .tripSection {
        height: 0px;

        float: left;

        border: 0;
        border-top: 2px dotted var(--lowContrastColor);
    }
}

</style>