<InfoRow>
    <span slot="left">{endName}</span>
</InfoRow>

<InfoRow>
    <span slot="left">
        {departureTimeString}
    </span>
    <span class="tripIcons" slot="center">
        {#each icons as icon}
            <span class="mdi mdi-{icon}"></span>
        {/each}
    </span>
    <span slot="right">
        {arrivalTimeString}
    </span>
</InfoRow>

<TripOverviewBar {steps} />

<script>
// Imports
import { getStepIcon } from "@/js/stepUtils.js";
import { timestampToString } from "@/js/utils/timeUtils.js";

// Components
import InfoRow from "@/components/Misc/InfoRow.svelte";
import TripOverviewBar from "@/components/TripOverviewBar.svelte";

// Props
export var trip;

// Computed
$: steps = trip.steps;
$: endName = trip.toAddress.streetName;
$: arrivalTimeString = timestampToString(trip.arrivalTime.timestamp);
$: departureTimeString = timestampToString(trip.departureTime.timestamp);
$: icons = getTripIcons(steps);

// Functions
function getTripIcons(steps) {
    return steps
        .map(step => getStepIcon(step));
}
</script>