{#if isCurrentStep && isFirstStep}
    <svelte:component this={LayoutCenter}>
        <div class="routeStep firstStep {travelMode}" style={inlineStyle}></div>
    </svelte:component>
{/if}
<svelte:component this={currentLayout}>
    <div class="routeStep {travelMode} {isCurrentStep && "current"}" style={inlineStyle}>
        <svelte:component this={stepInfoComponent} {step} />
    </div>
</svelte:component>
{#if isCurrentStep && isLastStep}
    <svelte:component this={LayoutCenter}>
        <div class="routeStep firstStep {travelMode}" style={inlineStyle}></div>
    </svelte:component>
{/if}

<script>
import { getStepColor, getStepBorderStyle, getStepInfoComponent } from "@/js/stepUtils.js";
import { onMount } from "svelte";
import { locationStore } from "@/js/locationUtils.js";
import { getCurrentStep, routeStore, getSteps } from "@/js/routeUtils.js";
import { toTimeString } from "@/js/timeUtils.js";

// Components
import LayoutCenter from "@/components/Layout/LayoutCenter.svelte";
import LayoutExpanded from "@/components/Layout/LayoutExpanded.svelte";

// Props
export var step;
export var isFirstStep = false;
export var isLastStep = false;
export var currentStep;

// Computed
$: isCurrentStep = currentStep;
$: travelMode = step.travelMode;
$: borderColor = getStepColor(step);
$: borderStyle = getStepBorderStyle(step);
$: stepInfoComponent = getStepInfoComponent(travelMode);
$: currentLayout = isCurrentStep ? LayoutExpanded : LayoutCenter;
$: inlineStyle = `border-color: ${borderColor}; border-style: ${borderStyle}`;

// Functions
// function getIsCurrentStep() {
//     var currentStep = getCurrentStep();
//     return currentStep.index === step.index;
// }
</script>

