<div class="trip">
    <TripHeader {trip} />

    {#if trainCancelled}
        <p class="cancelled">
            <span class="mdi mdi-alert"></span>
            Er is een trein die niet rijd
        </p>
    {/if}

    <div class="buttons">
        <SoftButton on:click="{() => selectTrip(trip)}" icon="play-circle-outline">
            Start
        </SoftButton>

        <SoftButton icon="plus-circle-outline" on:click={() => popupOpen = true}>
            Meer Info
        </SoftButton>
    </div>
</div>

<Popup bind:open={popupOpen}>
    <Trip {trip} />
</Popup>

<script>
// Imports
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Components
import Trip from "./Trip.svelte";
import TripHeader from "./Trip/TripHeader.svelte";
import SoftButton from "@/components/Misc/SoftButton.svelte";
import Popup from "@/components/Misc/Popup.svelte";

// Props
export var trip;

// Data
var popupOpen = false;

// Computed
$: trainCancelled = trip.steps
    .filter(step => step.travelMode === "train")
    .some(step => {
        if (step.journey === null) {
            return false;
        }

        return step.journey.cancelled === true;
    });

// Functions
function selectTrip(trip) {
    dispatch("selectTrip", trip);
}
</script>

