    padding: 8px 0;
    margin: 8px auto;
    box-sizing: border-box;

    &.open {
        padding: 24px 16px;

        border-radius: 10px;
        box-shadow: var(--defaultBoxShadow);
    }

    .buttons {
        width: 100%;

        margin: 8px 0;

        display: inline-block;

        & > :global(button) {
            width: 50%;

            float: left;
        }
    }

    .tripSteps :global(.routeStep) {
        width: calc(100% - 16px);
    }
}

.tripIcons {
    font-size: 18px;
}

.cancelled {
    color: var(--errorColor);
    text-align: center;
    font-weight: 700;
}

</style>