$height: 32px;

.searchBar, .searchBarResult {
    height: $height;
    width: 100%;

    display: flex;

    border: 1px solid var(--textColor);
    border-radius: var(--defaultBorderRadius);

    &.resultsAvailable {
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }

    .iconWrapper {
        height: 100%;
        width: $height;

        span {
            width: 100%;

            display: block;
            position: relative;
            top: 50%;
            transform: translateY(-50%);

            text-align: center;
            font-size: 20px;
        }
    }

    .textWrapper {
        height: 100%;

        position: relative;
        padding: 0 8px;
        box-sizing: border-box;
        flex: 1 1;

        p {
            width: calc(100% - 16px);

            margin: 0;
            padding: 0;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);

            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    input {
        height: calc(100% - 2px);

        flex: 1 1;
        margin: 0;
        margin-top: 1px;
        padding: 0 8px;

        outline: 0;
        border: 0;
        border-radius: inherit;
    }
}

.searchBarResult {
    border-top: 0;
    cursor: pointer;

    &:not(:last-child) {
        border-radius: 0;
        border-bottom: 0;
    }

    &:last-child {
        border-top-left-radius: 0;
        border-top-right-radius: 0;
    }
}

</style>