<button on:click={clickEvent} disabled={!enabled}>
    {#if iconPresent}
        <span class="mdi mdi-{icon}"></span>
    {/if}
    <slot />
</button>

<script>
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Props
export var icon = null;
export var enabled = true;

// Computed
$: iconPresent = icon !== null;

// Function
function clickEvent() {
    dispatch("click");
}
</script>

<style lang="scss">

button {
    width: 100%;

    margin: 16px auto;

    background-color: transparent;
    border: 1px solid var(--foregroundColor);
    border-radius: 5px;
    font-weight: 700;
    color: var(--foregroundColor);
    line-height: 16px;
    box-sizing: border-box;
    cursor: pointer;

    &:hover {
        background-color: var(--foregroundColor);
        color: var(--backgroundColor);
    }

    &:disabled {
        border-color: #696969;
        color: #696969;

        &:hover {
            background-color: #696969;
            color: var(--backgroundColor);
        }
    }

    span {
        font-size: 18px;
        line-height: 16px;
    }
}

</style>