<SpaceBetween>
    <AddressSearchBar query={addressObject.address} on:pickedResult={selectResult} />
    <ButtonContainer>
        <Button icon="content-save-outline" on:click={save}>Sla op</Button>
    </ButtonContainer>
</SpaceBetween>

<script>
// Imports
import { getAddressById, saveAddress } from "@/js/controllers/addresses.js";
import { createEventDispatcher } from "svelte";
const dispatch = createEventDispatcher();

// Components
import SpaceBetween from "@/components/Layout/SpaceBetween.svelte";
import AddressSearchBar from "./AddressSearchBar.svelte";
import ButtonContainer from "@/components/ButtonContainer.svelte";
import Button from "@/components/Button.svelte";

// Props
export var addressId;

// Data
var selectedResult = null;

// Computed
$: addressObject = getAddressById(addressId);

// Functions
function selectResult({ detail: result }) {
    console.log(result);
    selectedResult = result;
}

function save() {
    let addressObjectCopy = {...addressObject};

    if (selectedResult) {
        addressObjectCopy.location = selectedResult.location;
        addressObjectCopy.address = selectedResult.address;
    }

    saveAddress(addressId, addressObjectCopy);
    dispatch("saved");
}
</script>