<InfoRow>
    <span slot="left">{startTimeString}</span>
    <span slot="center">
        <HeadSign icon="train" {backgroundColor} {textColor}>{step.trainId}</HeadSign>
    </span>
    <span slot="right">{endTimeString}</span>
</InfoRow>

{#if journeyAvailable && cancelled}
    <p class="cancelled">
        <span class="mdi mdi-alert"></span>
        Deze trein is geannuleerd
    </p>
{/if}

<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-expand-right"></span> {step.fromStation.name}</span>
    <span slot="right">
        spoor {originPlatform}
        {#if originPlatformChanged}
            <span class="cancelledInformation">
                {plannedOriginPlatform}
            </span>
        {/if}
    </span>
</InfoRow>

<InfoRow>
    <span slot="left"><span class="mdi mdi-arrow-collapse-right"></span> {step.toStation.name}</span>
    <span slot="right">
        spoor {destinationPlatform}
        {#if destinationPlatformChanged}
            <span class="cancelledInformation">
                {plannedDestinationPlatform}
            </span>
        {/if}
    </span>
</InfoRow>

{#if journeyAvailable}
    <InfoRow>
        <span slot="left"><span class="mdi mdi-tape-measure"></span> Lengte</span>
        <span slot="right">
            <span class="lengthBoard">
                <span class="inner">
                    {journey.train.numberOfCarts}
                </span>
            </span>
        </span>
    </InfoRow>

    <MoreInfo>
        <p class="exitDirection">
            {#if journey.destinationExitSide === "left"}
                <span class="mdi mdi-chevron-triple-left"></span> Links uitstappen
            {:else}
                Rechts uitstappen <span class="mdi mdi-chevron-triple-right"></span>
            {/if}
        </p>

        <InfoRow>
            <span slot="left"><span class="mdi mdi-routes"></span> Richting</span>
            <span slot="right">{journey.directionStationName}</span>
        </InfoRow>

        {#each journey.train.materialParts as materialPart}
            <InfoRow>
                <span slot="left"><span class="mdi mdi-barcode"></span> Treinstel {materialPart.materialNumber}</span>
                <span slot="right">
                    {#each materialPart.facilities as facility}
                        <span class="facilityIcon mdi mdi-{getFacilityIcon(facility)}"></span>
                    {/each}
                </span>
            </InfoRow>

            <div class="trainImages">
                <img class="trainPartImage" src={materialPart.image} alt="Train part" />
            </div>
        {/each}
    </MoreInfo>
{/if}

