<script>
// Imports
import { onMount, onDestroy } from "svelte";
import { getStepColor, getStepTextColor } from "@/js/stepUtils.js";
import { timestampToString } from "@/js/utils/timeUtils.js";
import { createPoller } from "@/js/controllers/liveData.js";
import { getFacilityIcon } from "@/js/trainUtils.js";

// Components
import InfoRow from "@/components/Misc/InfoRow.svelte";
import HeadSign from "@/components/HeadSign.svelte";
import MoreInfo from "@/components/MoreInfo.svelte";

// Props
export var step;
console.log(step);

// Data
var journey = null;
var journeyPoller = null;

// Computed
$: journeyAvailable = journey !== null;
$: cancelled = journeyAvailable && journey.cancelled;
$: backgroundColor = getStepColor(step);
$: textColor = getStepTextColor(step);
$: startTimeString = timestampToString(step.departureTime.timestamp);
$: endTimeString = timestampToString(step.arrivalTime.timestamp);
$: originPlatform = journeyAvailable ? journey.actualDepartureTrack : "?";
$: destinationPlatform = journeyAvailable ? journey.actualArrivalTrack : "?";
$: plannedOriginPlatform = journeyAvailable ? journey.planedDepartureTrack : "?";
$: plannedDestinationPlatform = journeyAvailable ? journey.planedArrivalTrack : "?";
$: originPlatformChanged = originPlatform !== plannedOriginPlatform;
$: destinationPlatformChanged = destinationPlatform !== plannedDestinationPlatform;

// Events
onMount(() => {
    journeyPoller = createPoller({
        query: `
            query ($token: String!) {
                liveTrainJourney(journeyId: $token) {
                    refreshId
                    shouldPoll
                    pollAfter
                    journey {
                        cancelled
                        directionStationName
                        planedDepartureTrack
                        actualDepartureTrack
                        planedArrivalTrack
                        actualArrivalTrack
                        destinationExitSide
                        train {
                            id
                            numberOfCarts
                            materialParts {
                                materialNumber
                                facilities
                                image
                            }
                        }
                        currentPass {
                            status
                        }
                    }
                }
            }
        `,
        initialToken: step.journeyRefreshId,
        key: "liveTrainJourney",
        memoization: step.randomId,
        memoizationTags: ["liveBusJourney", "liveRouteJourney"],
        onData(data, instance) {
            if (!!data?.journey) {
                journey = data.journey;
                // currentPass = journey.currentPass;
            }
        },
    });
});

onDestroy(() => {
    if (journeyPoller) journeyPoller.stop();
    // if (locationPoller) locationPoller.stop();
});
</script>

