    --size: 24px;

    height: var(--size);
    width: var(--size);
    
    display: flex;
    transform: rotate(45deg);
    justify-content: center;
    align-items: center;

    background-color: #0072B7;
    color: #ffffff;
    border-radius: 3px;
    border: 1px solid #000000;
    box-sizing: border-box;

    .inner {
        display: block;
        transform: rotate(-45deg);
        text-align: center;
    }
}

.trainImages {
    width: 100%;

    margin-bottom: 16px;

    .trainPartImage {
        max-height: 32px;
        width: 100%;
    }
}

.facilityIcon {
    margin-left: 4px;
}

.exitDirection {
    text-align: center;
    // font-weight: 700;
}

.cancelled {
    color: var(--errorColor);
    text-align: center;
    font-weight: 700;
}

.liveDataActive {
    color: var(--liveDataActive);
}

</style>